.howitworks {
    position: relative;
    z-index: 10;
    color: $main-color-dark;
    &__container {
        padding: 88px 100px;
        @include md {
            padding: 40px 24px 60px; }
        @include sm {
            padding: 32px 10px 40px; } }

    &__title {
        margin-bottom: 50px;
        line-height: 1;
        @include sm {
            text-align: center; } }
    &__wrapper {
        display: flex;
        justify-content: space-between;
        @include sm {
            flex-direction: column; } }
    &__column {
        flex-basis: 45%;
        display: flex;
        flex-direction: column; } }

.work {
    position: relative;
    &:not(:last-child) {
        margin-bottom: 55px;
        @include sm {
           margin-bottom: 30px; } }
    @include sm {
        &:last-child {
            margin-bottom: 30px; } }
    &__icon {
        position: absolute;
        top: 9px;
        left: 0; }
    &__text {
        margin-left: 75px;
        font-size: 18px;
        font-weight: 300;
        line-height: 2;
        @include md {
            font-size: 14px;
            line-height: 1.5; } } }

