.news {
    color: #fff;
    margin-bottom: 110px;
    margin-top: 127px;
    @include sm {
        margin-bottom: 70px;
        margin-top: 70px; }
    @include md {
        margin-top: 60px; }
    &__container {
        position: relative; }
    &__title {
        margin-bottom: 48px; }
    &__wrapper {
        position: relative;
        display: flex;
        @include sm {
            flex-direction: column; } }
    &__btn-wrapper {
        display: flex;
        justify-content: center; }
    &__cube {
        position: absolute;
        right: -16vw;
        bottom: 20%; } }


.single-news {
    flex-basis: 30%;
    justify-content: space-between;
    margin-bottom: 35px;
    &:not(:last-child) {
        margin-right: 35px;
        @include sm {
            margin-right: 0; } }
    &__image {
        height: 300px;
        width: 100%;
        margin-bottom: 23px;
        border-radius: 8px;
        background-color: #222222;
        @include sm {
            height: 60vw; } }
    &__title {
        margin-bottom: 19px;
        font-size: 30px;
        font-weight: 600;
        line-height: 1.2; }
    &__text {
        font-size: 14pp;
        font-weight: normal;
        line-height: 1.57; } }
